import React from 'react';
import { Ul } from './../../styled/list';
import { Option, Property } from './styles';

//    
function OptionsList({ list = [], delimeter = ": " }) {
    return (
        <Ul>
            {list.map((option, index) => (
                <Option key={`tab${index * 10}`}>
                    <Property>
                        {option.property}
                        {delimeter}
                    </Property>
                    {option.value}
                </Option>
            ))}
        </Ul>
    );
}

export default OptionsList;